.banner-with-buttons-tabs {
  margin-top: var(--margins-top);
  margin-bottom: var(--margins-bottom);
  padding-top: var(--paddings-top);
  padding-bottom: var(--paddings-bottom);
  background: var(--gradient);

  @media (max-width: 767.98px) {
    margin-top: var(--margins-top--mobile);
    margin-bottom: var(--margins-bottom--mobile);
    padding-top: var(--paddings-top--mobile);
    padding-bottom: var(--paddings-bottom--mobile);
  }

  &[data-big-spacing='true'] {
    .banner-with-buttons-tabs__inner {
      padding: 48px 132px 117px 45px;

      @media (max-width: 767.98px) {
        padding: 32px 14px 31px 24px;
      }
    }
  }

  &__title {
    display: flex;
    justify-content: center;
    font-weight: 300;
    font-size: 40px;
    line-height: 100%;
    color: #3a4454;
    margin-bottom: 50px;
    text-align: center;
    // max-width: 932px;
    margin-left: auto;
    margin-right: auto;

    @media (max-width: 767.98px) {
      margin-bottom: 39px;
    }

    @media (max-width: 575.98px) {
      font-size: 32px;
    }
  }

  &__inner {
    position: relative;
    background: #ffffff;
    border: 1px solid #dce1e9;
    border-radius: 10px;
  }

  &__header {
    display: flex;
    justify-content: space-between;
  }

  &__tab {
    flex: 1 0 15%;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    text-align: center;
    color: #708090;
    background: #ebf0f5;
    padding: 16px 69px;
    transition: 0.3s;
    white-space: nowrap;
    display: flex;
    align-items: center;
    justify-content: center;

    @media (max-width: 767.98px) {
      white-space: normal;
      padding: 16px 20px;
    }

    &.active {
      background: #ffffff;
      color: #219963;
    }

    &:hover {
      color: #219963;
    }

    &:first-child {
      border-top-left-radius: 10px;
    }
    &:last-child {
      border-top-right-radius: 10px;
    }
  }

  // &__body {}

  &__content {
    display: none;
    padding: 53px 45px 45px 45px;

    @media (max-width: 767.98px) {
      padding: 32px 14px 31px 24px;
    }

    &.active {
      display: block;
    }
  }

  &__items {
    display: flex;
    justify-content: space-between;

    @media (max-width: 1199.98px) {
      flex-direction: column;
    }

    @media (max-width: 575.98px) {
      align-items: flex-start;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;

    &-image {
      width: 48px;
      height: 48px;
      flex-shrink: 0;
      margin-bottom: 20px;

      @media (max-width: 767.98px) {
        margin-bottom: 17px;
      }

      @media (max-width: 575.98px) {
        width: 50px;
        height: 50px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

    &-content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }

    &-title {
      font-weight: 700;
      font-size: 30px;
      line-height: 100%;
      color: #3a4454;
      max-width: var(--widths-title);

      @media (max-width: 767.98px) {
        font-size: 24px;
        max-width: var(--widths-title--mobile);
      }
    }

    &-description {
      font-size: 16px;
      line-height: 150%;
      color: #748994;
      margin-top: 20px;
      max-width: var(--widths-text);

      @media (max-width: 767.98px) {
        margin-top: 16px;
        max-width: var(--widths-text--mobile);
      }
    }

    &-button {
      display: inline-block;
      margin-top: 24px;
    }
  }

  &__item + &__item {
    margin-left: 50px;

    @media (max-width: 1199.98px) {
      margin-left: 0;
      margin-top: 36px;
    }
  }

  &__buttons {
    margin-top: 55px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    @media (max-width: 767.98px) {
      flex-direction: column;
      align-items: flex-start;
      margin-top: 39px;
    }
  }

  &__button {
    &.outlined-button,
    &.primary-button {
      @media (max-width: 767.98px) {
        padding-left: 10px;
        padding-right: 10px;
        width: 181px;
      }
    }
  }

  &__button + &__button {
    margin-left: 38px;

    @media (max-width: 767.98px) {
      margin-left: 0;
      margin-top: 16px;
    }
  }

  &__extra-text {
    margin-top: 48px;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;

    @media (max-width: 767.98px) {
      flex-direction: column;
    }

    &-content {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: #748994;
    }

    &-link {
      margin-left: 16px;
    }
  }

  &__hidden {
    visibility: hidden;
    opacity: 0;
    transition: 1s;
    transition-property: opacity;
    max-height: 0;

    &.text {
      a {
        transition: none;
      }
    }

    &.active {
      visibility: visible;
      opacity: 1;
      max-height: 2000px;
      margin-top: 24px;
    }
  }

  &__extra {
    .banner-with-buttons-tabs__grey-buttons {
      margin-top: 48px;
      border-top: 1px solid #dce1e9;
      display: flex;
      align-items: center;
      justify-content: center;
      padding-top: 24px;
    }

    .banner-with-buttons-tabs__grey-button {
      display: inline-flex;
      align-items: center;
      font-weight: 700;
      font-size: 16px;
      line-height: 100%;
      // text-transform: uppercase;
      color: #708090;
      text-align: center;

      &.active {
        img {
          transform: scale(1, -1);
        }
      }

      img {
        width: 18px;
        height: 16px;
        margin-left: 14px;
        transition: 0.3s;
      }
    }
  }
}
